<template>
    <ul id="changePassword">
        <li>
            <p class="leftCon"><span>手机号码</span></p>
            <input type="text" class="formInput" v-model="filePhone" readonly="readonly">
            <span :class="classA ? 'getCode' : 'reGetCode'" @click="getCode" v-text="classA ? countDownMsg : timesPhone"></span>
        </li>
        <li>
            <p class="leftCon les4"><span>验证码</span></p>
            <input type="number" class="formInput" v-model="code" placeholder="请输入手机验证码" >
        </li>
        <li>
            <p class="leftCon les4"><span>新密码</span></p>
            <input type="password" class="formInput" placeholder="请输入您的密码，长度为8-14位" v-model = "newPassword">
        </li>
        <li>
            <p class="leftCon"><span>确认密码</span></p>
            <input type="password" class="formInput" placeholder="再次输入新密码" v-model = "reNewPassword">
        </li>
        <big-btn :content="content" :topNum="topNum" :rotateShow="rotateShow" @childClick="sureSubmit"></big-btn>
    </ul>
</template>

<script>
import { apiUrl } from "@/common/js/api";
import { ajaxPost } from "@/common/js/public";
import { Toast, Notify } from "vant";
import bigBtn from "./userCommon/bigBtn";
export default {
  name: "changePassword",
  data() {
    return {
      phoneNumber: "",
      code: "",
      newPassword: "",
      reNewPassword: "",
      countDownMsg: "获取验证码",
      setDownMsg: "重新获取",
      classA: true,
      auth_time: 0,
      content: "确认信息",
      topNum: "3.28rem",
      rotateShow: false
    };
  },
  components: {
    bigBtn
  },
  created() {},
  mounted() {
    this.phoneNumber = JSON.parse(sessionStorage.getItem("userInfo")).phone;
  },
  methods: {
    getCode() {
      var _this = this;
      if (this.classA) {
        this.auth_time = 60;
        this.classA = false;
        var auth_timetimer = setInterval(() => {
          this.auth_time--;
          if (this.auth_time <= 0) {
            this.classA = true;
            clearInterval(auth_timetimer);
          }
        }, 1000);
        /* ajaxPost( apiUrl.getSystemSMS, {
          module: "resetPs",
          phone: _this.phoneNumber
        },
        function(data) { */
          //倒计时
          /* 
      1000 成功
      1001 参数为空
      1110 业务异常
      3004 发送太频繁
      1111 手机未注册
      3005 验证码次数已经达到上线
    */
          //var data = data.data.body;
          data = {
            resultCode: 1000
          }
          if (data.resultCode == 1000) {
            console.log("发送验证码成功");
          } else if (data.resultCode == 1001) {
            console.log("参数为空，验证码错误");
            _this.notifyIt("参数为空，验证码错误");
          } else if (data.resultCode == 1110) {
            console.log("业务异常");
            _this.notifyIt("业务异常");
          } else if (data.resultCode == 3004) {
            console.log("请不要频繁操作");
            _this.notifyIt("请不要频繁操作");
          } else if (data.resultCode == 1111) {
            console.log("手机号未注册");
            _this.notifyIt("手机号未注册");
          } else if (data.resultCode == 3005) {
            console.log("今天您的验证码次数已经达到上线");
            _this.notifyIt("今天您的验证码次数已经达到上线");
          }
        }
        /* function() {
          _this.notifyIt("网络繁忙");
        }
      )} */
    },
    notifyIt(msg, time = 1000) {
      this.$notify({
        message: msg,
        duration: time
      });
    },
    sureSubmit() {
      //确认信息
      var that = this;
      if (that.code == "") {
        that.notifyIt("验证码不能为空");
        return false;
      }
      if (that.newPassword == "") {
        that.notifyIt("新密码不能为空");
        return false;
      }
      if (that.newPassword.length < 8) {
        that.notifyIt("新密码长度不能少于8位");
        return false;
      }
      if (that.newPassword.length > 14) {
        that.notifyIt("新密码长度不能多于14位");
        return false;
      }
      if (that.reNewPassword == "") {
        that.notifyIt("确认密码不能为空");
        return false;
      }
      if (this.newPassword != this.reNewPassword) {
        that.notifyIt("新密码和确认密码不一致");
        return false;
      }
      this.rotateShow = true;
      /* ajaxPost( apiUrl.resetPassword, {
        phone: that.phoneNumber.toString(), //--手机号【必填】
        sixCode: that.code.toString(),
        newPassword: that.reNewPassword.toString()
      }, function(data) {
        var data = data.data.body; */
        that.rotateShow = false;
        console.log(data.resultCode);
        data = {
          resultCode: 1000
        }
        /*
    1000 成功
    1001 参数为空
    1111 手机号未注册
    3001 验证码错误
    3002 验证码失效
    */
        if (data.resultCode == 1000) {
          Toast.success("修改成功");
          that.$router.push({ name: "login" });
        } else if (data.resultCode == 1001) {
          console.log("无效操作");
          that.notifyIt("无效操作");
        } else if (data.resultCode == 3001) {
          console.log("验证码错误");
          that.notifyIt("验证码错误");
        } else if (data.resultCode == 3002) {
          console.log("验证码失效");
          that.notifyIt("验证码失效");
        }
      /* }, function(error) {
        that.notifyIt("网路繁忙");
      }); */
    }
  },
  computed: {
    filePhone: function() {
      var _leftNumber = this.phoneNumber.toString().substring(0, 3);
      var _rightNumber = this.phoneNumber
        .toString()
        .substring(7, this.phoneNumber.length);
      var result = _leftNumber + "****" + _rightNumber;
      return result;
    },
    timesPhone: function() {
      return "重新获取(" + this.auth_time + "s)";
    }
  },
  watch: {
    code(newVal) {
      this.code = this.code.substring(0, 6);
    }
  }
};
</script>

<style scoped lang="less">
#changePassword {
  width: 100%;
  box-sizing: border-box;
  li {
    width: 100%;
    box-sizing: border-box;
    padding: 0.232rem 0;
    input::-webkit-input-placeholder {
      color: #b1afaf;
      line-height: normal;
    }
    input::-moz-placeholder {
      color: #b1afaf;
      line-height: normal;
    }
    input:-moz-placeholder {
      color: #b1afaf;
      line-height: normal;
    }
    input:-ms-input-placeholder {
      color: #b1afaf;
      line-height: normal;
    }
    position: relative;
    height: 0.43 * 2rem;
    line-height: 0.43 * 2rem;
    padding-left: 0.15 * 2rem;
    padding-right: 0.15 * 2rem;
    border-bottom: 1px solid #f6f7f8;
    font-size: 0.14 * 2rem;
    background-size: 0.04 * 2rem 0.1 * 2rem;
    color: #272727;
    .leftCon {
      display: block;
      width: 0.76 * 2rem;
      float: left;
      overflow: hidden;
      line-height: normal;
      span {
        line-height: normal;
      }
    }
    .formInput {
      width: 4.4rem;
      height: 0.36rem;
      vertical-align: middle;
      line-height: normal;
      color: #272727;
      border: 1px solid transparent;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      font-size: 0.28rem;
      position: absolute;
      padding-top: 0.02rem;
      padding-bottom: 0;
      top: 0.232rem;
      left: 1.82rem;
      display: block;
    }
    .getCode {
      width: 0.84 * 2rem;
      height: 0.3 * 2rem;
      line-height: 0.3 * 2rem;
      box-sizing: border-box;
      border: 1px solid #3391e8;
      font-size: 0.12 * 2rem;
      display: block;
      position: absolute;
      text-align: center;
      color: #3391e8;
      top: 0.06 * 2rem;
      right: 0.15 * 2rem;
      /*margin-top: 0.06*2rem;*/
      border-radius: 2px;
    }
    .reGetCode {
      width: 0.84 * 2rem;
      height: 0.3 * 2rem;
      line-height: 0.3 * 2rem;
      box-sizing: border-box;
      border: 1px solid #b1afaf;
      font-size: 0.12 * 2rem;
      display: block;
      float: left;
      text-align: center;
      color: #b1afaf;
      position: absolute;
      top: 0.06 * 2rem;
      right: 0.15 * 2rem;
      border-radius: 2px;
    }
    .les4 {
      letter-spacing: 0.07 * 2rem;
    }
  }
  .sureButton {
    width: 3.2 * 2rem;
    height: 0.44 * 2rem;
    line-height: 0.44 * 2rem;
    text-align: center;
    background-color: #3391e8;
    color: #fff;
    margin: 1.64 * 2rem 0 0 0.27 * 2rem;
    border-radius: 4px;
    letter-spacing: 4px;
    font-size: 0.18 * 2rem;
    font-family: PingFangSC-Medium;
    box-shadow: 0px 4px 6px rgba(45, 136, 219, 0.3);
  }
}
</style>


